.congratulations__container {
}

.congratulations__navbar {
    margin: 0 0 150px 0;
}

.congratulations__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.congratulations__content > *:not(:last-child) {
    margin: 0 0 120px 0;
}

@media screen and (max-width: 1240px) {
    .congratulations__content {
        font-size: 22px;
    }
}

@media screen and (max-width: 640px) {
    .congratulations__content {
        font-size: 18px;
    }
}